.hlight { color: $color-secondary }
.center { text-align: center }
.smaller { font-size: 80% }
.italic { font-style: italic }
.right { text-align: right }
.clear { clear: both }

#products td:last-child { vertical-align: top }
#products td img { max-width: none }
.logo { line-height: 112%; color: $color-primary; font-family: 'DejaVu Sans Mono','Courier New', monospace }

#computers td:first-child { width: 320px }

#projects_main { margin: 0 -3% }
#projects_main img { margin-bottom: 10px }
#projects_main a { font-weight: bold }
#projects_main li { width: 18%; display: inline-block; text-align: center; vertical-align: text-top; margin: 15px 3%; font-size: 80% }

.div_article ul strong, .div_article ol strong { color: $color-list; font-weight: 500; text-shadow: 0.5px 0.16px 0 $color-primary; }
.logo { width: 100%; overflow: hidden; margin: 0; border: 0 }
#portrait { float: right; width: 200px; margin-left: 30px }
.pubdate { font-style: italic; font-size: 90% }
#bestview { margin-top: 50px }


#computers td { vertical-align: top }

.header .btn_text { text-transform: uppercase }
.header .btn_text:hover { color: $color-secondary }
.header .header_btn, .header .span_right { margin: 0 0.6em 0 0.06em; list-style: none; text-decoration: none }

@media screen and (max-width: 800px) {
  #products td, #products tr, #products table { display: block }
  #products td:first-child { border-bottom: 0 }
  #products td:last-child { border-top: 0 }
  #projects_main li { display: inline-block; width: 42%; }
  .logo { font-size: 58% }
}

@media screen and (max-width: 420px) {
  .logo { font-size: 2.5vw }
  #computers .nophoto { display: none }
  #computers table, #computers td, #computers tr { display: block; border: 0 }
  #computers td:first-child { width: auto }
}

@media screen and (max-width: 420px) {
  #projects_main li { display: block; width: 100%; }
  #portrait { float: none; margin: auto; width: auto; text-align: center }
  .header .header_btn, .header .span_right { margin: 0 1.1vw 0 1.1vw; }
}

@media screen and (max-width: 1280px) {
  .page { background-image: url('../img/bg1280.jpg'); }
}

@media screen and (min-width: 1281px) and (max-width: 1366px) {
  .page { background-image: url('../img/bg1366.jpg'); }
}

@media screen and (min-width: 1281px) and (max-width: 1536px) {
  .page { background-image: url('../img/bg1536.jpg'); }
}

@media screen and (min-width: 1537px) and (max-width: 1600px) {
  .page { background-image: url('../img/bg1366.jpg'); } 
}

@media screen and (min-width: 801px) and (max-width: 1537px) {
  .nav_bar .header_btn i { display: none }   
}

@media screen and (min-width: 801px) and (max-width: 1200px) {
  .nav_bar .header_btn:last-child { display: none } 
}

@media screen and (min-width: 801px) and (max-width: 1200px) {
  .nav_bar .header_btn:nth-last-child(2) { display: none }   
  .logo { font-size: 2.1vw; }
}

@media screen and (min-width: 801px) and (max-width: 1024px) {
  .nav_bar .span_right { display: none }
}

.div_article { top: 40px }
.biografy_nav a { margin: 0 5em }

.footer_counters { margin: 20px auto 3px auto; }
.footer_counters a { display: inline-block; margin: 0 20px; height: 33px; overflow: hidden }
#cookie_warn2:target { display: inline }
